import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import { fetchBitcoin } from '../actions/bitcoin'

export const Loot = ({ balance, bitcoin, fetchBitcoin }) => {
  const [ bitcoinBalance, setBitcoinBalance ] = useState('')

  useEffect(() => {
    fetchBitcoin()
  }, [])

  useEffect(() => {
    setBitcoinBalance(computeBitcoin())
  }, [balance, bitcoin])

  const computeBitcoin = () => {
    if (Object.keys(bitcoin).length === 0) return ''

    return balance / parseInt(bitcoin.bpi.USD.rate.replace(',', ''), 10)
  }

  return (
    <h3>
      Bitcoin Balance: { bitcoinBalance }
    </h3>
  )
}

const mapStateToProps = state => ({
  balance: state.balance,
  bitcoin: state.bitcoin
})

export default connect(mapStateToProps, {
  fetchBitcoin
})(Loot)
