import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import { deposit, withdraw } from '../actions/balance'

export const Wallet = ({ balance, deposit, withdraw }) => {
  const [ balanceInput, setBalanceInput ] = useState('')

  const onDepositClick = () => {
    deposit(balanceInput)
  }

  const onWithdrawClick = () => {
    withdraw(balanceInput)
  }

  useEffect(() => {
    setBalanceInput('')
  }, [balance])

  return (
    <div>
      <h3 className='balance'>Wallet balance: $ {balance}</h3>
      <br />
      <input className='input-wallet' onChange={(e) => setBalanceInput(parseInt(e.target.value, 10))} value={balanceInput} />
      <button className='btn-deposit' onClick={onDepositClick}> Deposit </button>
      <button className='btn-withdraw' onClick={onWithdrawClick}> Withdraw </button>
    </div>
  )
}

const mapStateToProps = state => ({
  balance: state.balance
})

export default connect(mapStateToProps, {
  deposit,
  withdraw
})(Wallet)
